import React from"react";import Prayers from'./Prayers-min'; import Testimonies from'./Testimonies-min'; import{makeStyles}from"@material-ui/core/styles"; import Tabs from'@material-ui/core/Tabs';import Tab from'@material-ui/core/Tab';const PT=()=>{const[selectedTab,setselectedTab]=React.useState(0);const handleChange=(event,newValue)=>{setselectedTab(newValue);};const useStyles=makeStyles({tabsStyle:{background:'white',color:"Black"}});const classes=useStyles();return(<div className='prayers-testimonies'id='PT'><><Tabs className={classes.tabsStyle}value={selectedTab}onChange={handleChange}centered><Tab label="Prayers"/><Tab label="Testimonies"/></Tabs>{selectedTab===0&&<Prayers/>} {selectedTab===1&&<Testimonies/>}</></div>);};export default PT;