import React from "react";
import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Header from "./Header-min";
import Banner from "./Banner-min";
import Carousel from "./Carousel-min";
import About from "./About-min";
import Blog from "./Blog-min";
import PT from "./PT-min";
import Connect from './Connect-min';
function App() {
  return (
    // BEM
    <Router>
      <div className="App">
        <Switch>
          <Route path="/Home">
            <Banner />
            <Header />

            <Carousel />
            <About />
            <Blog />
            <PT />
          <Connect/>
          </Route>

          <Route path="/">
            <Banner />
            <Header />
            <Carousel />
            <About />
            <Blog />
            <PT />
            <Connect/>
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
